<template>
	<div class="iconWrap">
		<slot name='Img'></slot><br/>
		<span v-text='txt'></span>
	</div>
</template>
<script type="text/javascript">
	export default {
		props:['txt']
	}
</script>
<style type="text/css">
	.iconWrap {width: 25%;float: left; padding: 20px 0}
	.iconWrap img{ width: 30px }
	.iconWrap span{font-size: 12px; color: #666 }
</style>


