<template>
		<div :class="{itmeWrap:true,a_ac:!bol }" @click='fn()'>
			<span v-if='bol'><slot name='normalImg'></slot><br/></span>
			<span v-else='bol'><slot name='activeImg'></slot><br/></span>
			<span v-text='txt' :class='{ green:!bol}'></span>
		</div>
</template>
<script type="text/javascript">
		
	export default {
		props:['txt','id','isTrue'],
		computed:{
			bol:function(){
				if(this.$parent.sel == this.id){
					return false
				}
				return true
			}
		},
		methods:{
			fn(){
				this.$emit('change',this.id)
				this.$router.push({
					path:'/'+this.id,
					query:{
						// name:this.isTrue
					}
				});
			}
		}

	}	


</script>
<style type="text/css">
	.itmeWrap{
		width: 20%;
		float: left;
	}
	.itmeWrap img{ width: 40px }
	.itmeWrap span{ font-size: 12px; color: #999 }
	.itmeWrap .green { color:#42bd56; }
</style>


