<template>
	<div class="tvWrap">
		<LbWrap>
			<h3 slot='h3'></h3>
			<div class="contWrap" slot='banner'>
				<Banner swipeid='tv1' :pagination='false' :slidesPerView='1.2' :loop='false' :freeMode='true'>
					<div class="swiper-slide" slot='swiper-slide' v-for='item in tv1'>
						<Piclist>
							<img :src="item.cover" slot='img'>
							<div class="right">
								<span slot='title' class="title">{{ item.title }}</span><br/>
								<span class="xx"></span><span class="xx"></span><span class="xx"></span><span class="xx"></span><span class="xx"></span>
								<span slot='rate'>{{ item.rate }}</span><br/>
								<span>54/</span><br/>
								<span>{{ item.cover_y }} </span>
							</div>
						</Piclist>
					</div>
				</Banner>
			</div>
		</LbWrap>
		<LbWrap>
			<h3 slot='h3'></h3>
			<Banner swipeid='tv2' :pagination='false' :slidesPerView='3.5' :loop='false' :freeMode='true' slot='banner'>
				<div class="swiper-slide" slot='swiper-slide' v-for='item in tv2'>
					<Piclist>
						<img :src="item.cover" slot='img'>
						<span slot='rate'>{{ item.rate }}</span>
						<span class="xx"></span><span class="xx"></span><span class="xx"></span><span class="xx"></span><span class="xx"></span>
						<span slot='title' class="title">{{ item.title }}</span>
					</Piclist>
				</div>
			</Banner>
		</LbWrap>
		<LbWrap>
			<h3 slot='h3'></h3>
			<MyMain v-for='item in tv3' :key='item.id' :maincont='item' slot='banner' id='audio/film'>
				<img :src="item.target.cover_url" slot='img'>
				<h3 slot='title'>{{ item.title }}</h3>
				<p slot='passage'>{{ item.target.desc }}</p>
				<p slot='author' class="left">:<span>{{ item.target.author.name }}</span></p>
				<p slot='from' class="right">{{ item.source_cn }}</p>
			</MyMain>
		</LbWrap>
	</div>
</template>
<script type="text/javascript">
	import Banner from '@/components/banner'
	import Piclist from '@/components/piclist'
	import LbWrap from '@/components/lbWrap'
	import MyMain from '@/components/main'

	export default {
		components:{
			Banner,
			Piclist,
			LbWrap,
			MyMain
		},
		data:function(){
			return {
				tv1:[],
				tv2:[],
				tv3:[]
			}
		},
		mounted:function(){
			this.axios.get('/api/tvData').then((rep)=>{
				this.tv1 = rep.data.data.subjects.slice(0,4)
				this.tv2 = rep.data.data.subjects.slice(5,13)
			})
			this.axios.get('/api/homeData').then((rep)=>{
				this.tv3 = rep.data.data.recommend_feeds.slice(16,20)
			})
		}
	}
</script>
<style type="text/css">
	
	.tvWrap .tv1 .swiper-slide { border:1px solid #eee; margin-left: 10px}
	.tvWrap .tv1 .swiper-slide img{ padding: 10px }
	.tvWrap .tv1 .swiper-slide .right { position: absolute;left: 120px; top: 10px;text-align: left; }
</style>


