<template>
	<div class="musicWrap">
		<LbWrap>
			<h3 slot='h3'></h3>
			<Banner swipeid='city1' :pagination='false' :slidesPerView='3.5' :loop='false' :freeMode='true' slot='banner'>
				<div class="swiper-slide" slot='swiper-slide' v-for='item in music1'>
					<Piclist>
						<img :src="item.cover" slot='img'>
						<span slot='rate'>{{ item.rate }}</span>
						<span class="xx"></span><span class="xx"></span><span class="xx"></span><span class="xx"></span><span class="xx"></span>
						<span slot='title' class="title">{{ item.title }}</span>
					</Piclist>
				</div>
			</Banner>
		</LbWrap>
		<LbWrap>
			<h3 slot='h3'></h3>
			<Banner swipeid='city1' :pagination='false' :slidesPerView='3.5' :loop='false' :freeMode='true' slot='banner'>
				<div class="swiper-slide" slot='swiper-slide' v-for='item in music2'>
					<Piclist>
						<img :src="item.cover" slot='img'>
						<span slot='rate'>{{ item.rate }}</span>
						<span class="xx"></span><span class="xx"></span><span class="xx"></span><span class="xx"></span><span class="xx"></span>
						<span slot='title' class="title">{{ item.title }}</span>
					</Piclist>
				</div>
			</Banner>
		</LbWrap>
	</div>
</template>
<script type="text/javascript">
	import Banner from '@/components/banner'
	import Piclist from '@/components/piclist'
	import LbWrap from '@/components/lbWrap'
	import MyMain from '@/components/main'

	export default {
		components:{
			Banner,
			Piclist,
			LbWrap,
			MyMain
		},
		data:function(){
			return {
				music1:[],
				music2:[]
			}
		},
		mounted:function(){
			this.axios.get('/api/tvData').then((rep)=>{
				this.music1 = rep.data.data.subjects.slice(30,35)
				this.music2 = rep.data.data.subjects.slice(35,39)
			})
		}
	}
</script>
<style type="text/css">
</style>


