<template>
	<div class="mainlistWrap">
		<div class="top">
			<img src="../assets/images/ic_bar_back_green.png" @click='home()' class="home">
			<ul class="piclist">
				<li><img src="../assets/images/ic_menu_donate_green.png" class="fenxiang"></li>
				<li><img src="../assets/images/ic_menu_like.png" class="fenxiang"></li>
				<li><img src="../assets/images/ic_menu_reply.png" class="fenxiang"></li>
				<li><img src="../assets/images/ic_action_share_green.png" class="fenxiang"></li>
			</ul>
		</div>
		<div class="contWrap">
			<h3 slot='title'>{{ arr.title }}</h3>
			<img :src="arr.target.cover_url" slot='img'>
			<p slot='passage' class="desc">{{ arr.target.desc }}</p>
			<p slot='author' class="left">:<span>{{ arr.target.author.name }}</span></p>
			<p slot='from' class="right">{{ arr.source_cn }}</p>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default {
		data:function(){
			return {
				arr:[],
				id:''
			}
		},
		methods:{
			home:function(){
				this.$router.push('/'+this.id);
			document.getElementById('aaa').style.opacity = 1
			},
		},
		created:function(){
			this.arr = this.$route.query.cont;
			// console.log(this.$route.query.cont)
			this.id = this.$route.query.id
		}
	}
</script>
<style type="text/css">
	.mainlistWrap .top { height: 48px; background-color: #F3F2F2; width: 100%; border-bottom: 1px solid #ccc; text-align: left; line-height: 48px }
	.mainlistWrap .top img { width: 20px; padding-top: 15px; padding-right: 10px  }
	.mainlistWrap .top .home { padding-left: 10px }
	.mainlistWrap .top .piclist{ float: right; list-style: none; height: 48px }
	.mainlistWrap .top .piclist li{ float: left; }
	.mainlistWrap .contWrap { padding: 10px; font-size: 14px; }
	.mainlistWrap .contWrap img { padding: 10px }
	.mainlistWrap .contWrap .left,.right,.desc{  padding-top: 10px;text-align: left; }
	.mainlistWrap .contWrap .desc{ text-indent: 2em; }
</style>


