<template>
	<div class="groupWrap">
		<MyHearder>
			<span></span>
			<img src="../../assets/images/ic_actionbar_search_icon.png" slot='leftImg'>
			<img src="../../assets/images/ic_chat_green.png" slot='rightImg'>
		</MyHearder>
		<div class="tuijian">
			<h3>418902</h3>
			<p></p>
			<span></span>
		</div>
		<div class="pic">
			<span></span>
		</div>
		<!--  -->
		<div class="movies">
			<h3></h3>
			<GroupList v-for='item in movies' :title='item.title' :p='item.p' :num='item.num' @click='getClass' :key='item.id'>
				<img src="../../assets/images/media.jpg" slot='img'>
			</GroupList>
		</div>
		<div class="movies">
			<h3></h3>
			<GroupList v-for='item in book' :title='item.title' :p='item.p' :num='item.num' @click='getClass' :key='item.id'>
				<img src="../../assets/images/media.jpg" slot='img'>
			</GroupList>
		</div>
		<div class="batch">
			<a class="left"></a>
			<a :class="{right:true,green:num>0}">,</a>
		</div>
	</div>
</template>
<script type="text/javascript">
	import MyHearder from '@/components/hearder'
	import GroupList from '@/components/grouplist'

	export default {
		components:{
			MyHearder,
			GroupList
		},
		data:function(){
			return {
				movies:[
					{	
						id:301,
					 	title:'',
						p:'?????',
						num:'7074'
					},
					{	
						id:302,
						title:'-Desperate',
						p:'',
						num:'17726'
					},
					{
						id:303,
						title:'',
						p:'?????',
						num:'7074'
					},
				],
				book:[
					{	
						id:401,
						title:'',
						p:'',
						num:'29307'
					},
					{
						id:402,
						title:'',
						p:'',
						num:'67442'
					},
					{
						id:403,
						title:'KINDLE',
						p:'',
						num:'241256'
					},
				],
				num:0
			}
		},
		methods:{
			getClass:function(bol){
				if(bol){
					this.num++
				}else{
					this.num--
				}
			}
		}
	}

</script>
<style type="text/css">
	.groupWrap { background-color: #fff; }
	.groupWrap .tuijian{ width: 100%; height: 130px; background-image: url(../../assets/images/2017060412210150388.png); background-size: cover; position: relative; }
	.groupWrap .tuijian h3 { padding-top: 40px }
	.groupWrap .tuijian p{ color: #999; font-size:14px; padding-top: 10px}
	.groupWrap .tuijian span { position: absolute; bottom: 10px; left: 10px; font-size: 12px ; color: #999}
	.groupWrap .pic { background-image: url(../../assets/images/ic_rec_group_banner_6.png); background-size: cover; width: 90%; height: 60px; margin: 0 auto;position: relative;}
	.groupWrap .pic span {position: absolute; bottom: 2px; left: 10px; font-size: 12px ; color: #fff }
	.groupWrap .movies { width: 90%; margin: 0 auto; border-top: 1px solid #ccc; margin-top: 20px;position: relative; background-color: #fff}
	.groupWrap .movies h3{ position: absolute; top: -10px; left: 38%; font-size: 14px; font-weight: normal; background-color: #fff; padding: 0 20px }
	.groupWrap .batch{position: fixed;left: 0; bottom: 65px;width: 100%; height: 40px; background-color: #fff;}
	.groupWrap .batch a{ font-size: 12px;}
	.groupWrap .batch .left{ float: left; padding-left: 40px; background-image: url(../../assets/images/ic_reshare_flag.png); background-size: 20px 20px;background-repeat: no-repeat; background-position: 15px 10px; color: #058805;line-height: 40px  }
	.groupWrap .batch .right{ display: inline-block; float: right; margin-right: 20px; padding: 7px; background-color: #A1A1A1; height: 10px;line-height: 10px;margin-top: 5px;border-radius: 4px;color: #fff }
	.groupWrap .batch .green { background-color: #058805  }
</style>


