<template>
	<div class='headerWrap' :style='{backgroundColor:col}'>
		<slot></slot>
		<span class="leftimg"><slot name='leftImg'></slot></span>
		<span class="rightimg"><slot name='rightImg'></slot></span>
	</div>
</template>
<script type="text/javascript">
	export default {
		props:{
			col:{
				default:'#fff'
			}
		}
	}
</script>
<style type="text/css">
	
	.headerWrap{
		width: 100%;
		height: 48px;
		border-bottom: 1px solid #ccc;
		line-height: 48px;
		position: relative;
	}
	.headerWrap img { width: 22px;}
	.headerWrap .rightimg,.leftimg {position: absolute; top:10px;}
	.headerWrap .rightimg{  right: 20px  }
	.headerWrap .leftimg { right: 70px }
	.headerWrap .title{color:#4c4c4c;}
</style>


