<template>
	<div>
		<h1>Parent,page:{{ msg }}</h1>
		<Child :change='getVal'></Child>
	</div>
</template>
<script type="text/javascript">

	import Child from './Child'
	
	export default {
		data:function () {
			// body...
			return {
				msg:4
			}
		},
		components:{ Child },
		methods:{
			getVal:function(i){
				this.msg = i
			}
		}

	}


</script>


