<template>
	<div class="bookWrap">
		<LbWrap>
			<h3 slot='h3'></h3>
			<Banner swipeid='book1' :pagination='false' :slidesPerView='3.5' :loop='false' :freeMode='true' slot='banner'>
				<div class="swiper-slide" slot='swiper-slide' v-for='item in book1'>
					<Piclist>
						<img :src="item.cover" slot='img'>
						<span slot='rate'>{{ item.rate }}</span>
						<span class="xx"></span><span class="xx"></span><span class="xx"></span><span class="xx"></span><span class="xx"></span>
						<span slot='title' class="title">{{ item.title }}</span>
					</Piclist>
				</div>
			</Banner>
		</LbWrap>
		<LbWrap>
			<h3 slot='h3'></h3>
			<Banner swipeid='book2' :pagination='false' :slidesPerView='3.5' :loop='false' :freeMode='true' slot='banner'>
				<div class="swiper-slide" slot='swiper-slide' v-for='item in book2'>
					<Piclist>
						<img :src="item.cover" slot='img'>
						<span slot='rate'>{{ item.rate }}</span>
						<span class="xx"></span><span class="xx"></span><span class="xx"></span><span class="xx"></span><span class="xx"></span>
						<span slot='title' class="title">{{ item.title }}</span>
					</Piclist>
				</div>
			</Banner>
		</LbWrap>
	</div>
</template>
<script type="text/javascript">
	import Banner from '@/components/banner'
	import Piclist from '@/components/piclist'
	import LbWrap from '@/components/lbWrap'

	export default {
		components:{
			Banner,
			Piclist,
			LbWrap,
		},
		data:function(){
			return {
				book1:[],
				book2:[],
			}
		},
		mounted:function(){
			this.axios.get('/api/tvData').then((rep)=>{
				this.book1 = rep.data.data.subjects.slice(13,19)
				this.book2 = rep.data.data.subjects.slice(19,25)
			})
		}
	}
</script>
<style type="text/css">

</style>


