<template>
	<div class="mainWrap" @click='fn()'>
		<div class="m_top">
			<slot name='img'></slot>
			<slot name='title'></slot>
			<slot name='passage'></slot>
		</div>
		<i></i>
		<div class="m_bot clearfix">
			<slot name='author'></slot>
			<slot name='from'></slot>
			<!-- <p class="left">:<span>{{ author }}</span></p>
			<p class="right">:<span >{{ from }}</span></p> -->
		</div>
	</div>
</template>
<script type="text/javascript">
	
	export default {
		props:['maincont','id'],
		methods:{
			fn:function(){
				this.$router.push({
					path:'/manlist',
					query:{
						cont:this.maincont,
						id:this.id
					}
				})

				document.getElementById('aaa').style.opacity = 0
				
				console.log(this.id)
			}
		}
	}

</script>
<style type="text/css">
	.mainWrap{ width: 100%;margin: 0 auto; text-align: left; border-bottom: 1px solid #DEDEDE; background-color: #fff }
	.mainWrap .m_top,.m_bot{ padding: 5px 20px }
	.mainWrap .m_top img{ float: right; width: 100px;}
	.mainWrap .m_top h3 { font-size: 16px;  }
	.mainWrap i{ width: 20px; height: 1px; background-color: #ccc; display: block; margin-top: 10px; margin-left: 20px }

	.mainWrap .m_top p { font-size: 14px; color: #999; padding-top: 10px; width: 65%;height: 60px;overflow : hidden;
text-overflow: ellipsis;
display: -webkit-box;
-webkit-line-clamp: 3;
-webkit-box-orient: vertical;}
/*
-webkit-line-clamp 
display: -webkit-box;   
-webkit-box-orient   
text-overflow: ellipsis; */


	.mainWrap .m_bot { line-height: 40px; font-size: 12px; color: #999;padding-top: 20px }
	.mainWrap .m_bot .left{ float: left; }
	.mainWrap .m_bot .right{ float: right; }
</style>


