<template>
	<div class="homeWrap">
		<MyHearder col='#42c055' lef='320px'>
			<div class="search">
				<img src="../../assets/images/ic_search.png" class="s_img_l">
				<input type="text" placeholder="   "></input>
				<img src="../../assets/images/ic_scan_gray.png" class="s_img_r">
			</div>
			<img src="../../assets/images/ic_chat_white.png" slot='rightImg'>
		</MyHearder>

		<MyBanner swipeid='homeBanner' paginationDirection='right' :autoplay='2000' :slidesPerView='1'>
			<img src="../../assets/images/banner/01.jpg" slot='swiper-slide' class="swiper-slide">
			<img src="../../assets/images/banner/02.jpg" slot='swiper-slide' class="swiper-slide">
			<img src="../../assets/images/banner/03.jpg" slot='swiper-slide' class="swiper-slide">
		</MyBanner>

		<div class="h_plate">
			<h3 class="plate host"></h3>
			<MyMain v-for='item in host' :key='item.id' :maincont='item' id='home'>
				<img :src="item.target.cover_url" slot='img'>
				<h3 slot='title'>{{ item.title }}</h3>
				<p slot='passage'>{{ item.target.desc }}</p>
				<p slot='author' class="left">:<span>{{ item.target.author.name }}</span></p>
				<p slot='from' class="right">{{ item.source_cn }}</p>
			</MyMain>
		</div>

		<div class="h_plate">
			<h3 class="plate sunday"></h3>
			<MyMain v-for='item in sunday' :key='item.id' :maincont='item' id='home'>
				<img :src="item.target.cover_url" slot='img'>
				<h3 slot='title'>{{ item.title }}</h3>
				<p slot='passage'>{{ item.target.desc }}</p>
				<p slot='author' class="left">:<span>{{ item.target.author.name }}</span></p>
				<p slot='from' class="right">{{ item.source_cn }}</p>
			</MyMain>
		</div>

		<div class="h_plate">
			<h3 class="plate sunday"></h3>
			<MyMain v-for='item in yike' :key='item.id' :maincont='item' id='home'>
				<img :src="item.target.cover_url" slot='img'>
				<h3 slot='title'>{{ item.title }}</h3>
				<p slot='passage'>{{ item.target.desc }}</p>
				<p slot='author' class="left">:<span>{{ item.target.author.name }}</span></p>
				<p slot='from' class="right">{{ item.source_cn }}</p>
			</MyMain>
		</div>

	</div>
</template>
<script type="text/javascript">
	import MyHearder from '@/components/hearder'
	import MyBanner from '@/components/banner'
	import MyMain from '@/components/main'

	export default {
		components:{
			MyHearder,
			MyBanner,
			MyMain
		},
		data:function(){
			return {
				host:[],
				sunday:[],
				yike:[],
			}
		},
		mounted:function(){
			this.fn();
		},
		methods:{
			fn:function(){
				this.axios.get('/api/homeData').then((rep)=>{
					//console.log(rep.data.data.recommend_feeds.slice(0,3))
					 this.host = rep.data.data.recommend_feeds.slice(0,5)
					 this.sunday = rep.data.data.recommend_feeds.slice(6,10)
					 this.yike = rep.data.data.recommend_feeds.slice(11,16)
					 // console.log(this.sunday)
				})
			}
		}
	}

</script>
<style type="text/css">
	.homeWrap .search{ position: relative; }
	.homeWrap .search input{ width: 70%;position: absolute; left: 15px; top: 14px; height: 25px; border-radius: 5px; border:none;  padding-left: 30px; outline: none;}
	.homeWrap .search .s_img_l{ position: absolute;top: 17px; left: 18px;z-index: 1; width: 17px }
	.homeWrap .search .s_img_r{  position: absolute;top: 17px; right: 75px;z-index: 1; width: 17px  }
	.homeWrap .headerWrap{ border-bottom: none; }
	.homeWrap .h_plate { background-color: #fff; padding-top: 5px; margin-bottom: 10px; }
	.homeWrap .plate{  text-align: left;font-size: 14px; padding-left: 10px; margin-bottom: 5px}
	.homeWrap .host,.sunday{ border-left: 3px solid #FEBA3E;}
	.homeWrap .host { color: #FEBA3E;  }
	.homeWrap .sunday { border-color: #31FB35 }
	.homeWrap .headerWrap { position: fixed;z-index: 2; top: 0; left: 0; }
	.homeWrap .homeBanner { margin-top:48px; }
</style>


