					// console.log(this.arr)
				})
				// console.log(this.arr)
				// return this.arr
			}
		 },
		mounted:function(){
			this.qingqiu();
			// console.log(this.arr)
		},
		
	}

</script>
<style type="text/css">
	.nav{background-color: #fff;}
	.audioWrap .itmeWrap span:nth-child(1){display: none;}
	.audioWrap .itmeWrap { padding: 5px 0; }
	.audioWrap .itmeWrap span { font-size: 16px;color:#666; }
	.audioWrap .a_ac{ border-bottom: 3px solid #65F46E; }
	.audioWrap .a_ac span{color: #65F46E}
</style>


