<template>
	<div>
		<ul>
			<li @click='fn(1)'><a href="##"></a></li>
			<li @click='fn(num-1)'><a href="##"></a></li>
			<li @click='fn(num-3)' v-if='num-3>0'><a href="##" v-text='num-3'></a></li>
			<li @click='fn(num-2)' v-if='num-2>1'><a href="##" v-text='num-2'></a></li>
			<li @click='fn(num-1)' v-if='num-1>1'><a href="##" v-text='num-1'></a></li>
			<li class="active"><a href="##" v-text='num'></a></li>
			<li @click='fn(num+1)' v-if='num+1<max'><a href="##" v-text='num+1'>5</a></li>
			<li @click='fn(num+2)' v-if='num+2<max'><a href="##" v-text='num+2'>6</a></li>
			<li @click='fn(num+3)' v-if='num+3<max'><a href="##" v-text='num+3'>7</a></li>
			<li @click='fn(num+1)'><a href="##"></a></li>
			<li @click='fn(max)'><a href="##"></a></li>
		</ul>
		<div>
			<small> <span>{{ num }}</span> ,<span>{{max}}</span></small>
			<input type="number" v-model='gotoPage'></input><button @click='fn(gotoPage)'>Go</button>
		</div>
	</div>
</template>
<script type="text/javascript">
	
	export default {
		data:function () {
			// body...
			return {
				num:4,
				max:100,
				gotoPage:''
			}
		},
		props:['change'],
		methods:{
			fn(i){

				i = parseInt(i)
				
				if(i>this.max || i<1 || !i){

					this.gotoPage='';
					
					return;
				}

				this.num = i;	

				this.gotoPage = ''

				this.change(i);
				
				this.gotoPage='';
			}
		}
	}

</script>
<style type="text/css">
	ul{ display: inline-block; }
	li{ list-style: none; float: left;padding: 5px 10px; border: 1px solid #ccc; line-height: 25px; width: 50px}
	a{ text-decoration: none; }
	.active{ background-color: lightblue }
</style>


