
<template>
    <div class="swiper-container" :class='swipeid'>
        <div class="swiper-wrapper">
            <slot name='swiper-slide'></slot>
        </div>
        <div :class="{'swiper-pagination':pagination}" :style='{"text-align":paginationDirection}'></div>
    </div>
</template>
<style type="text/css">
    
    @import '../assets/libs/swiper/css/swiper.css';
    .swiper-container{ width: 100%;  margin: auto; }
    .swiper-container img{ width: 100%;height: 125px;  }

</style>

<script type="text/javascript">
    
    import '@/assets/libs/swiper/js/swiper.js'

    export default {
        props:{
            swipeid:{
                type:String,
                default:''
            },
            pagination:{
                type:Boolean,
                default:true
            },
            paginationDirection:{
                type:String,
                default:'center'
            },
            effect:{
                type:String,
                default:'slide'
            },
            loop:{
                type:Boolean,
                default:true
            },
            direction:{
                type:String,
                default:'horizontal'
            },
            autoplay:{
                type:Number,
                default:0
            },
            paginationType:{
                type:String,
                default:'bullets'
            },
            slidesPerView:{
                type:Number,
                default:0
            },
            spaceBetween:{
                type:Number,
                default:0
            },
            freeMode:{
                type:Boolean,
                default:false
            },
            isTrue:{
                type:Boolean,
                default:false
            }
        },
        // watch:{
        //     'isTrue':function(){
        //        this.$parent.qingqiu()
        //     }
        // },
        mounted:function(){
            
                var That = this;

                new Swiper('.'+That.swipeid,{
                    
                    loop:That.loop,     //

                    pagination:'.swiper-pagination',  //

                    paginationType:That.paginationType, //

                    autoplay:That.autoplay, //

                    direction:That.direction, // 

                    effect:That.effect,  // 

                    slidesPerView: That.slidesPerView, // 

                    spaceBetween: That.spaceBetween, //

                    observer: true,//swiperswiper

                    observeParents: true,//swiperswiper

                    // slidesOffsetAfter:0,

                    // slidesOffsetBefore:0,

                    freeMode : That.freeMode, 
                    //falseslidewrappertruefreeslide
                    // initialSlide:1
                    // freeModeMomentum:true

                    // freeModeMomentumBounce:false
                });
                // this.$nextTick(function(){
                //     this.isTrue = true
                // })
              
        }

    }

</script>



